"use client"

import React, { useState, useRef, useEffect } from "react";
import Link from "next/link";
import { Book, FileText, BarChart2, MessageCircle, FolderOpen, ChevronDown, ChevronUp, FileStack, FileSignature, FileSearch, FileSpreadsheet, FlaskConical, Users, Video, Calendar, BookOpenCheck, Lightbulb } from "lucide-react";

const dropdowns = [
  {
    label: "Study Materials",
    icon: Book,
    items: [
      { label: "JEE Notes", href: "/study-materials/jee-notes", icon: FileText },
      { label: "EAPCET Notes", href: "/study-materials/eapcet-notes", icon: FileStack },
      { label: "Formula Sheets", href: "/study-materials/formula-sheets", icon: FileSpreadsheet },
      { label: "Previous Year Papers", href: "/study-materials/previous-papers", icon: FileSearch },
    ],
  },
  {
    label: "Mock Tests",
    icon: FlaskConical,
    items: [
      { label: "JEE Main Mock Tests", href: "/mock-tests/jee-main", icon: FileSignature },
      { label: "JEE Advanced Mock Tests", href: "/mock-tests/jee-advanced", icon: FileSignature },
      { label: "EAPCET Mock Tests", href: "/mock-tests/eapcet", icon: FileSignature },
      { label: "Chapter-wise Tests", href: "/mock-tests/chapter-wise", icon: FileSignature },
    ],
  },
  {
    label: "Resources",
    icon: FolderOpen,
    items: [
      { label: "Video Lectures", href: "/resources/video-lectures", icon: Video },
      { label: "Reference Books", href: "/resources/reference-books", icon: BookOpenCheck },
      { label: "Exam Calendar", href: "/resources/exam-calendar", icon: Calendar },
      { label: "Tips & Tricks", href: "/resources/tips-tricks", icon: Lightbulb },
    ],
  },
];

function useClickOutside(ref: React.RefObject<HTMLElement | null>, handler: () => void) {
  useEffect(() => {
    const listener = (event: MouseEvent) => {
      if (!ref.current || ref.current.contains(event.target as Node)) {
        return;
      }
      handler();
    };
    document.addEventListener("mousedown", listener);
    return () => document.removeEventListener("mousedown", listener);
  }, [ref, handler]);
}

export default function Navbar() {
  const [openDropdown, setOpenDropdown] = useState<string | null>(null);
  const dropdownRefs = dropdowns.map(() => useRef<HTMLDivElement | null>(null));

  useEffect(() => {
    const handleEsc = (e: KeyboardEvent) => {
      if (e.key === "Escape") setOpenDropdown(null);
    };
    document.addEventListener("keydown", handleEsc);
    return () => document.removeEventListener("keydown", handleEsc);
  }, []);

  dropdownRefs.forEach((ref, idx) => {
    useClickOutside(ref, () => openDropdown === dropdowns[idx].label && setOpenDropdown(null));
  });

  return (
    <header className="container mx-auto px-8 max-w-7xl py-4 flex justify-between items-center">
      <div className="flex items-center">
        <Link href="/">
          <img src="/recallpro.png" alt="RecallPro Logo" width={48} height={48} className="mr-2 rounded" />
        </Link>
      </div>
      <nav className="hidden md:flex items-center gap-4 lg:gap-8 font-medium">
        {/* New Buttons (left of Features) */}
        {dropdowns.map((dropdown, idx) => {
          const Icon = dropdown.icon;
          const isOpen = openDropdown === dropdown.label;
          return (
            <div key={dropdown.label} className="relative" ref={dropdownRefs[idx]}>
              <button
                className={`flex items-center gap-1 px-3 py-2 rounded-md transition-colors hover:bg-blue-50 focus:outline-none focus:ring-2 focus:ring-blue-400 ${isOpen ? "bg-blue-100" : ""}`}
                aria-haspopup="true"
                aria-expanded={isOpen}
                aria-controls={`${dropdown.label}-menu`}
                onClick={() => setOpenDropdown(isOpen ? null : dropdown.label)}
                onKeyDown={e => {
                  if (e.key === "Enter" || e.key === " ") setOpenDropdown(isOpen ? null : dropdown.label);
                }}
              >
                <Icon className="w-5 h-5 text-blue-600" />
                <span>{dropdown.label}</span>
                {isOpen ? <ChevronUp className="w-4 h-4 ml-1" /> : <ChevronDown className="w-4 h-4 ml-1" />}
              </button>
              {isOpen && (
                <div
                  id={`${dropdown.label}-menu`}
                  className="absolute left-0 mt-2 w-56 bg-white border border-gray-200 rounded-md shadow-lg z-50 animate-fade-in"
                  role="menu"
                  tabIndex={-1}
                >
                  {dropdown.items.map(item => (
                    <Link
                      key={item.label}
                      href={item.href}
                      className="flex items-center gap-2 px-4 py-2 hover:bg-blue-50 focus:bg-blue-100 transition-colors text-gray-700"
                      role="menuitem"
                      tabIndex={0}
                    >
                      <item.icon className="w-4 h-4 text-blue-500" />
                      {item.label}
                    </Link>
                  ))}
                </div>
              )}
            </div>
          );
        })}
        {/* Existing Buttons */}
        <a href="#features" className="text-gray-600 hover:text-blue-600 px-3 py-2 rounded-md transition-colors">Features</a>
        <a href="#test-series" className="text-gray-600 hover:text-blue-600 px-3 py-2 rounded-md transition-colors">Test Series</a>
        <a href="#pricing" className="text-gray-600 hover:text-blue-600 px-3 py-2 rounded-md transition-colors">Pricing</a>
        <Link href="/recall" className="bg-blue-600 text-white px-4 py-2 rounded-md hover:bg-blue-700 transition">Recall</Link>
        <Link href="/auth/login" className="bg-blue-600 text-white px-4 py-2 rounded-md hover:bg-blue-700 transition">Get Started</Link>
        {/* Analytics Button */}
        <Link href="/analytics" className="flex items-center gap-2 px-3 py-2 rounded-md hover:bg-blue-50 transition-colors text-gray-700" aria-label="Analytics">
          <BarChart2 className="w-5 h-5 text-blue-600" /> Analytics
        </Link>
        {/* Doubt Forum Button */}
        <Link href="/doubt-forum" className="flex items-center gap-2 px-3 py-2 rounded-md hover:bg-blue-50 transition-colors text-gray-700" aria-label="Doubt Forum">
          <MessageCircle className="w-5 h-5 text-blue-600" /> Doubt Forum
        </Link>
      </nav>
      {/* TODO: Add mobile menu */}
    </header>
  );
} 